import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useStudentAdmissionFeeList } from "../../../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import CustomCreateButton from "../../../../Ui/Button/CustomCreateButton";
import { useEffect, useState } from "react";
import NotifyEvent from "../../../../Utils/Snackbar/NotifyEvent";
import { pdfCdnUrl } from "../../../../Services/UrlConfig/BaseUrl";
const AfterCollectFeesViewReceipt = ({
  onClose,
  applicant,
  appId,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const [data, setData] = useState(null);
  const { studentAdmissionFeeList, studentAdmissionFeeListRefetch } =
    useStudentAdmissionFeeList({
      sid: applicant?.student?._id,
      skip: !applicant?.student?._id,
    });

  useEffect(() => {
    if (studentAdmissionFeeList) {
      for (let dfg of studentAdmissionFeeList) {
        if (dfg?.appId?._id === appId) {
          if (
            dfg?.applicable_card?.remaining_array?.[0]?.fee_receipt
              ?.receipt_file
          ) {
            setData(
              dfg?.applicable_card?.remaining_array?.[0]?.fee_receipt
                ?.receipt_file
            );
            setSnackbar({
              open: false,
              msg: "",
              variant: "error",
            });
          }
        }
      }
    }
  }, [studentAdmissionFeeList]);

  const onGenerating = () => {
    if (!data) {
      setSnackbar({
        open: true,
        msg: "Fee receipt generating in process. Please try after some time.",
        variant: "error",
      });
      studentAdmissionFeeListRefetch();
    }
  };
  return (
    <>
      <Modal onClose={() => {}}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("collect_fees_successfully")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <div
              className={style.modal_container_header}
              style={{
                flexDirection: "column",
                gap: "0",
              }}
            >
              <p
                style={{
                  marginBottom: "0.5rem",
                }}
              >
                {t("payment_of_student")}
              </p>
              <h6>
                {`${applicant?.student?.studentFirstName ?? ""} ${
                  applicant?.student?.studentMiddleName
                    ? applicant?.student?.studentMiddleName
                    : ""
                } ${applicant?.student?.studentLastName ?? ""}`}
              </h6>
            </div>
            {data ? (
              <a
                href={`${pdfCdnUrl}/${data}`}
                target="_blank"
                rel="noopener noreferrer"
                onClick={onRefetch}
              >
                <CustomCreateButton
                  label={t("view_receipt")}
                  customStyle={{
                    "--ccb-bg": "#def3e1",
                    "--ccb-b": "#008000",
                    "--ccb-text": "#008000",
                    "--ccb-bg-h": "#def3e1",
                    "--ccb-text-h": "#008000",

                    justifyContent: "Center",
                    marginBlock: "1rem",
                  }}
                />
              </a>
            ) : (
              <CustomCreateButton
                label={t("view_receipt")}
                onAction={onGenerating}
                customStyle={{
                  "--ccb-bg": "#def3e1",
                  "--ccb-b": "#008000",
                  "--ccb-text": "#008000",
                  "--ccb-bg-h": "#def3e1",
                  "--ccb-text-h": "#008000",

                  justifyContent: "Center",
                  marginBlock: "1rem",
                }}
              />
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default AfterCollectFeesViewReceipt;
